@import '../../styles/variables.scss';

.optionItem {
  position: relative;
  width: fit-content;
  margin-bottom: 10px;

  &:hover {
    .optionItemShape {
      stroke: $primaryOrange;
    }
  }

  .optionItemShape {
    fill: $primaryWhite;
    stroke: $primaryGrey;
    transition: stroke 0.3s ease-in-out;
  }

  .optionItemLabel {
    position: absolute;
    top: 50%;
    left: 15%;
    transform: translate(-15%, -50%);
    font-size: 20px;

    .optionItemLabelLetter {
      text-transform: capitalize;
      font-weight: 600;
      color: $primaryOrange;
      margin-right: 11px;
    }
  }
}
